* {
  margin: 0%;
  padding: 0%;
}

body {
  background-color: black;
}

/*Search*/
.search {
  display: flex;
  background-image: url("./Assets/cool-background.png");
  min-height: 111vh;
  width: 100%;

}

form {
  position: relative;
  left: 40%;
  top: 75px;

}

input {
  height: 32px;
  width: 222px;
  border: 1px solid black;
  outline: none;
  margin: 6px;
  border-radius: 11px;
}

input::placeholder {
  font-size: 14px;
}

button {
  margin: 6px;
  padding: 6px;
  background-color: rgb(250, 14, 140);
  color: white;
  font-weight: bolder;
  cursor: pointer;
  border-radius: 8px;
  border: 2px solid silver;
  transition: all ease-in 0.8s;
}
button:hover{
  transform: translateY(2px) scale(1.1);
}
button:nth-child(3){
  transition: all ease-in 0.8s;
}
button:nth-child(3):hover{
  border: 4px solid red;
  color: red;
  background-color: white;
}

.Searchresult {
  position: relative;
  top: 88px;
  right: 30%;
  transition: all 0.4s;
  box-sizing: border-box;

}
.Searchresult:hover{
  transform: translateY(-11px) scale(1.1);
}

.Searchresult img {
  width: 800px;
  height: 455px;
  border: 4px solid rgb(255, 251, 251);
  border-radius:14px ;
  cursor: pointer;
  transition: all 3.6s;
}
.Searchresult img:hover{
border: 4px solid red;
}
